@import url('https://fonts.googleapis.com/css?family=Montserrat:400,400i,700,700i');

html {
  box-sizing: border-box;
  font-size: 14px;
  font-family: 'Montserrat', sans-serif;
  line-height: 1.2;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

html, body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
}

*, :after, :before {
  box-sizing: inherit;
}

main {
  position: relative;
  perspective: 150px;
}

.intro-carousel {
  position: absolute;
  z-index: 99999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  img {
    display: none;
  }
}

.flickity-viewport {
  height: 100% !important;
}

.intro-cell {
  width: 100%;
  height: 100%;
  z-index: 10000;
  text-align: center;
}

.intro-text {
  position: absolute;
  left: 50%;
  bottom: 5%;
  transform: translateX(-50%);
  max-width: 860px;

  h1 {
    font-size: 42px;
    margin: 0;
  }

  h2 {
    font-size: 16px;
    text-transform: uppercase;
    margin: 0;
    
  }

  p {
    font-size: 18px;
    line-height: 1.5;
    
  }

  a {
    display: inline-block;
    padding: 15px 30px;
    background: #feca0a;
    border-radius: 99px;
    text-transform: uppercase;
    font-weight: bold;
  }

  .feature {
    height: 800px;
  }
}
#spring-text {
  position: absolute;
  left: 0px;
  top: 250px;
  width: 100vw;
  height: 100vh;
}

.gif-background {
  position: fixed;
  top: 50%; /* Centraliza verticalmente */
  left: 0; /* Move o gif para o canto esquerdo */
  transform: translateY(-50%) rotate(-15deg); /* Corrige a posição central e aplica rotação lateral */
  z-index: 99991; /* Mantém o gif à frente de outros elementos */
  width: auto;
  height: 50%; /* Tamanho do GIF ajustável */
  pointer-events: none; /* Evita interferência com outros elementos */
  opacity: 0.9; /* Controle de opacidade */
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.3), 0 5px 15px rgba(0, 0, 0, 0.2); /* Sombra moderna ao redor do GIF */
  border: 2px solid rgba(255, 255, 255, 0.5); /* Borda estilizada */
  border-radius: 10px; /* Suaviza os cantos da borda */
}

.gif-background::after {
  content: ''; /* Elemento fictício para criar o efeito de reflexo */
  position: absolute;
  bottom: -10px; /* Posiciona o reflexo abaixo do GIF */
  left: 0;
  width: 150px;
  height: 0;
  background: rgba(255, 255, 255, 0.1); /* Reflexo suave */
  transform: scaleY(-1); /* Inverte o reflexo verticalmente */
  filter: blur(4px); /* Aplica um leve desfoque para suavizar o reflexo */
  opacity: 0.5; /* Controle da opacidade do reflexo */
}


